<template>
  <div id="app">
    <nav>
      
      <div class = 'nav-wrapper black' >
        
        <div class = 'container head'>
          <a href = '' class = 'right brand-logo' v-on:click.prevent = "showEditor = true"> new post</a>
          
          <a href="#" data-target="mobile-demo" class="sidenav-trigger"><i class="material-icons">menu</i></a>
          <ul class="left hide-on-med-and-down nav-links-list">
            
           
          </ul>
        </div>
      </div>
    </nav>
    <div class = "editor-wrapper" v-if = "showEditor"  v-on:click.self  = "showEditor = false">
      <div class = "container">
      <postEditor class="col s12"  
                    v-on:close = "showEditor = false"></postEditor></div>
    </div>
    <auth></auth>

    <ul class="sidenav" id="mobile-demo">
        <li><a href="#">Steam</a></li>
        
       
    </ul>
    <router-view class = "container"></router-view>
  </div>
</template>

<script>
import auth from './components/auth.vue'
export default {
  name: 'app',
  components : {
    auth
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      showEditor : false
    }
  },
  mounted(){
    var elems = document.querySelectorAll('.sidenav');
    var instances = M.Sidenav.init(elems);
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active   2.1.8 */ {
  opacity: 0;
}
</style>




