<template>
    <form class = "post-editor white card row">
        <div class = "input-field col s12">
            <textarea id="header" v-model = "title" v-bind:placeholder = "title" class="materialize-textarea col s10"></textarea>
            
            <i class="material-icons right green-text waves-effect waves-light" v-on:click = "sendData()">check</i>
            <i class="material-icons right red-text waves-effect waves-light" v-on:click = "$emit('close')">close</i>
        </div>
        <div class = "input-field col s12">
            <textarea id="text" v-bind:placeholder = "text" v-model = "text" class="materialize-textarea col s12"></textarea>
            
        </div>
    </form>
</template>
<script>
import {mapGetters } from 'vuex'
export default {
    props:{
        mode : {
            default(){
                return 'create'
            }
        },
        post:{
            type : Object,
            default(){
                return {
                    id : 1,
                    title : 'Header',
                    text : 'Text'
                }
            }
        }
    },
    data(){
        return{
            text : this.post.text,
            title : this.post.title,
            
        }
    },
    computed :{
        ...mapGetters(['getToken'])
    },
    methods:{
        sendData(){
            
            
            (this.mode === 'update' ? this.$backend.put('/post/' + this.post.id, { text : this.text, title : this.title},
            {headers : {
                    Authorization : this.getToken
                } }) : 
                this.$backend.post('/post', { text : this.text, title : this.title}, {headers : {
                    Authorization : this.getToken
                } }, ))
                .then(()=>{
                     this.$emit('update')
                     this.$emit('close')
                })
           
        }
    }
}
</script>
<style lang="scss">
.post-editor{
   padding: 0!important;
   
}
</style>



