<template>
    <div class = 'auth'>
        
        <div class = "fixed-action-btn">
          <a class="btn-floating btn-large waves-effect waves-light black" v-if = "!authenticated" v-on:click = "showForm = true">
              <i class="material-icons">add</i>
          </a>
          <router-link :to = "'/user/?id=' + getUser.id" class="btn-floating btn-large waves-effect waves-light black" v-if = "authenticated" >
              <i class="material-icons">person</i>
          </router-link>
          <a class="btn-floating btn-large waves-effect waves-light black" v-if = "authenticated" v-on:click = "Logout()">
              <i class="material-icons">directions_walk</i>
          </a>
        </div>
        <transition name = 'fade'>
        <div class = "wrapper " v-if = "showForm" v-on:click.self = "showForm = false">
            <div class = "auth_menu card col s8 m6 row">
                
                    <div class = 'col s6 signin_btn ' v-on:click = "showSignin = true ; showSignup = false"  v-bind:class = "{active : showSignin == true}">SIGNIN</div>
                    <div class = 'col s6 signup_btn ' v-on:click = "showSignin = false ; showSignup = true" v-bind:class = "{active : showSignup == true}">SIGNUP</div>
                
                <form  class = 'signin container' v-if = "showSignin" @submit.prevent = "Signin(model)">
                    
                    
                    
                <div class="row ">
                    <div class="input-field col s12 ">
                            <input  id="first_name" type="text" class="validate" v-model.lazy = "model.username">
                            <label for="first_name">First Name</label>
                        </div>
                        <div class="input-field col s12 ">
                            <input id="password" type="password" class="validate" v-model.lazy = "model.password">
                            <label for="password">Password</label>
                        </div>
                    
                        
                </div>
                <div class = "row">
                    <div class = "col s12 offset-s3">
                            <button class="btn waves-effect waves-light orange darken-4" type="submit" name="action">Log in
                                <i class="material-icons right">send</i>
                            </button>
                        </div>
                </div>    
                </form>
                <form  class= 'signup container' v-if = "showSignup" @submit.prevent = "Signup(model)" >
                    
                <div class="row">
                    <div class="input-field col s12">
                            <input  id="first_name" type="text" class="validate" v-model.lazy = "model.username">
                            <label for="first_name">First Name</label>
                        </div>
                        <div class="input-field col s12">
                            <input id="password" type="password" class="validate" v-model.lazy = "model.password">
                            <label for="password">Password</label>
                        </div>
                    
                        
                </div>
                <div class = "row ">
                    <div class = "col s12 offset-s3 ">
                            <button class="btn waves-effect waves-light orange darken-4" type="submit" name="action">Signup
                                <i class="material-icons right">send</i>
                            </button>
                        </div>
                </div>    
                </form>
                <div class = "col s12">
                    {{message}}
                </div>
            </div>
        </div>
        </transition>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';



export default {
    data(){
        return {
            showSignin : true,
            showSignup : false,
            model : {
                username : '',
                password : ''
            },
            showForm : false,
            message : '',
            authenticated : ''
           
        }
    },
    methods:{
        ...mapActions(['signin','signup','logout']),
        ...mapGetters(['getAuthStatus']),
        Signup(model){
            this.signup(model).then(res => {
                this.message = res.message
                this.CheckAuth()
                
            }).catch(err => {
                this.message = err
            })
        },
        Signin(model){
            this.signin(model).then(res => {
                this.message = res.message;
                this.CheckAuth()
            }).catch(err => {
                this.message = err
            })
        },
        Logout(){
            this.logout()
            this.authenticated = false
        },
        CheckAuth(){
            this.getAuthStatus().then( res => {
                this.authenticated = res.authenticated
            }).catch(err =>{
                console.log(err)
            })
        }
    },
    computed:{
        ...mapGetters(['getUser','getToken'])
    },
    created(){
        this.CheckAuth();
        console.log(typeof this.getToken)
    },
   
}
</script>
<style lang="scss">

.auth{
    .wrapper{
        position: fixed;
        z-index:10;
        background-color: rgba(0, 0, 0, 0.486);
        top:0;
        left:0;
        height: 100vh;
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center
    }
    .auth_menu{
        .signin_btn{
            border-radius : 2px 0 0 0;
        }
        .signup_btn{
            border-radius : 0 2px 0 0;
        }
        .signup_btn, .signin_btn{
            padding:0;
            display: flex;
            justify-content: center;
            transition: 0.2s;
            height:2em;
            cursor:pointer;
            &.active{
                background-color:orange;
                color:white
            }
        }
        
        .container{
            width:70%;
        }
    }
}

</style>




