<template>
    <div class = "post card row">
        <transition name = "fade">
            <div class = "editor-wrapper " v-if = "showEditor" v-on:click.self  = "showEditor = false">
                <div class = "container">
                    <postEditor class="col s12" v-bind:post = "post" v-bind:mode = "'update'" v-on:update = "fetchData()"
                    v-on:close = "showEditor = false"></postEditor>
                </div>
                
            </div>
        </transition>
        
        <h5 class = "post__title col s12">{{post.title}}
            <i class="material-icons right" v-on:click = "showEditor = true" v-if = "getUser().id== post.creator_id">edit</i>
            <i class="material-icons right" v-on:click = "deleteBtnWorker()" v-if = "getUser().id == post.creator_id">delete</i>
        </h5>
        <div class = "col s4 post__author"></div>
        <div class = "post__edit-btn col s1"></div>
        <div class = "post__delete-btn col s1"></div>

        <div class = "post__text col s12">{{post.text}}</div>
        <ul class="collection col s12">
            <li class="collection-item avatar col s12 " v-for = "(item,i) in comments" v-bind:key = "i">
                <i class="material-icons circle green">insert_chart</i>
                <span class="title">{{item.creator_id}}</span>

                <p>{{item.text}} 
                </p>
                <p class = "time">{{item.createdAt}}</p>
                
            </li>
            
        </ul>
        <transition name = "fade">
         <form class="col s12" v-if = "authStatus">
            <div class="row">
                <div class="input-field col s8">
                    <textarea id="textarea1" class="materialize-textarea" v-model = "commentText"></textarea>
                    <label for="textarea1">Textarea</label>
                </div>
                <button class="btn waves-effect waves-light col s2 input-field" type="submit" v-on:click.prevent = 'sendComment()'>Submit
                    <i class="material-icons right">send</i>
                </button>
            </div>
            
        </form>
        </transition>
    </div>
</template>
<script>
import postEditor from './post-editor.vue'
import {mapGetters} from 'vuex'
export default {
 
    props:{
        id:{
            type : Number,
            required : true
        }
    },
    data(){
        return{
            post:
            {
                id : this.id,
                title : '',
                text : ''
            },
            comments : [],
            commentText : '',
            authStatus : false,
            showEditor : false,
            user_id : this.getUser().id
            
        }
    },
    methods: {
        fetchData(){
            this.$backend.get('/post/id=' + this.id).then(res =>{
                console.log(res)
                this.post = res.data[0]
            })
        },
        fetchComments(){
           this.$backend.get('/comment/post_id=' + this.id).then(res =>{
                this.comments = res.data
            })
        },
        async deletePost(){
           await this.$backend.delete('/post/' + this.post.id,{headers : {
                    Authorization : this.getToken
                } })
        },
          sendComment(){
           this.$backend.post('/comment', {text : this.commentText,post_id : this.post.id}, {headers : {
                    Authorization : this.getToken
                } }).then(() =>{
               this.fetchComments()
           })
        },
        updateComment(id){
            this.$backend.put('/comment/' + id, { text : this.commentText })
        },
        async deleteBtnWorker(){
            await this.deletePost()
            this.$emit('delete')
        },
       
        ...mapGetters(['getUser'])
    },
    computed : {
        ...mapGetters(['getToken','getAuthStatus'])
    },
    created(){
        this.fetchData()
        this.fetchComments()
       this.getAuthStatus.then(res=>{
           this.authStatus = res.authenticated
       })
      
    },
    watch :{
        getAuthStatus(promise){
            promise.then(result => {
                this.authStatus = result.authenticated
                
            })
        }
    }
}
</script>
<style lang="scss">
    .post{
        text-align: start;
        .post__text{
            padding-bottom: 2em;
        }
        
        .collection{
            border-left-color: #0000;
            border-right-color: #0000;
            padding-top:2em;
            .collection-item{
                padding-top:1em;
                .time{
                    font-size:0.7em
                }
            }
        }
        
        
    }
    .editor-wrapper{
        
        position : fixed;
        z-index : 20;
        top : 0;
        left : 0;
        width:100vw;
        height : 100vh;
        display:flex;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.726);
        align-items: center;
        justify-content: center;
        .container{
            margin:0;
            margin-left : -2%;
            padding : 0
        }
    }
    
</style>


