<template>
    <div class = "home">
       
        <transition-group name = "list-complete">
            <post v-for = "(item,id) in posts" v-bind:key = "item.id" v-bind:id = "item.id"
            v-on:delete = "posts.splice(id,1)" class = "list-complete-item"></post>
        </transition-group>
        
    </div>
</template>
<script>
import post from './post.vue'
import axios from 'axios';

export default {
    components : {
        post,
        
    },
    props : {
        query : {
            type : String,
            default(){
                return 'all'
            }
        }
    },
    data(){
        return{
            posts : []
        }
    },
    methods  : {
        fetchData(){
            console.log(this.query)
            this.$backend.get('/post/' + this.query).then(res =>{
                this.posts = res.data
            })
        }
    },
    created(){
        this.fetchData()
    },
    watch:{
        $route(){
            this.fetchData()
        }
    }
}
</script>
<style lang="scss" scoped>
.list-complete-item {
  transition: transform 0.5s;
  display: inline-block;
  
}
.list-complete-enter, .list-complete-leave-to
/* .list-complete-leave-active   2.1.8 */ {
  opacity: 0;
  
}
.list-complete-leave-active {
  position: absolute;
}
</style>



