import axios from 'axios'
import config from '../../../config.json'
const state = {
    user: localStorage.getItem('user'),
    token:localStorage.getItem('token'),
    authStatus: false
}

const mutations = 
{
    setUser(state,data)
    {
        state.user = data.user
        state.token = data.token
        if(data.user == null || data.token == null)state.authStatus = false
        else state.authStatus = true
    },
   
    
}
 const actions = 
 {
     signin({commit}, {username,password})
     {
         return new Promise(function(resolve,reject){
            axios.post(config.api + '/signin',{username,password},{withCredentials: true})
            .then(res => {
               
                if(res.status == 200)
                {
                    if(res.data.authorized==true)
                     {
                        localStorage.setItem('user',JSON.stringify(res.data.details.user))
                        localStorage.setItem('token',res.data.details.token)
                        commit('setUser',{user:JSON.stringify(res.data.details.user),token : res.data.details.token})
                        
                    }
                    resolve({authenticated:res.data.authorized,message:res.data.message})
                }
                else{
                    reject('error')
                }
            }).catch(err => {console.log(err)})
         })
         
        
     },
     signup({commit},{username,password})
     {
        console.log({username,password})
        return new Promise(function(resolve,reject){
            axios.post(config.api + '/signup',{username,password},{withCredentials: true})
            .then(res => {
                
                if(res.status == 200)
                 {
                     if(res.data.authorized==true)
                     {
                        localStorage.setItem('user',JSON.stringify(res.data.details.user))
                        localStorage.setItem('token',res.data.details.token)
                        commit('setUser',{user:JSON.stringify(res.data.details.user),token : res.data.details.token})
                        
                     }
                     
                     resolve({authenticated:res.data.authorized,message:res.data.message})
                 }
                 else{
                     reject('error')
                 }
            }).catch(err => {console.log(err)})
        })
       
     },
     logout({commit})
     {
        
                localStorage.removeItem("user")
                localStorage.removeItem("token")
                commit('setUser',{user : null, token : null})
                         
         
     }
 }

 const getters = 
 {
     getUser : () =>
     {
         return ( state.user == null || state.user == undefined ) ? {username : null, id : null } : JSON.parse(state.user)
     },
     getToken : () =>{
         return state.token
     },
     getAuthStatus : () =>
     {
         
         return new Promise(function(resolve,reject){
            axios.get(config.api + '/auth',{withCredentials: true,
            headers:{
                'Authorization' : state.token
            }
            }).then( res => {
                if(res.status == 200)
                    resolve({authenticated:res.data.authorized,message:res.data.message})
                else
                    reject('error')
            })
         })
     }
    

 }

 export default {
     state,
     mutations,
     actions,
     getters
 }


